import { isArray, slice, flatten } from 'lodash';


/**   */
class Token {
	/**
     *   . 
     * @param {int} index -   .
     * @param {*} data -  .
     * @param {Scanner} scanner -   {@link Scanner}.
    */
	constructor(index, data, scanner){
		this._index = index
		this.data = data
		this.scanner = scanner
	}

	/**
     *   
     * @param {int}  count - .
     
     * @return {token} -   {@link token}     {count} .
    */
		
	atLeft(count){
		return this.scanner.atLeft(this, count)
	}	

	/**
     *   
     * @param {int}  count - .
     
     * @return {token} -   {@link token}     {count} .
    */
	
	atRight(count){
		return this.scanner.atRight(this, count)
	}

	/**
     *     
     * @param {int}  pos - .
     
     * @return {token} -   {@link token}   {pos}.
    */

	atIndex(pos){
		return this.scanner.token(pos)	
	}		
	
	/**
     *   
     * @return {token} -   {@link token}.
    */

	next(token){ 
		return this.scanner.next(this)
	}	

	/**
     *   
     * @return {token} -   {@link token}.
    */

	prev(token){
		return this.scanner.prev(this)
	}	
	
	/**
     *    
     * @param {int} count -  .
     * @return {token} -   {@link token}.
    */
	
	arroundLeft(count){
		return this.scanner.arroundLeft(this, count)
	}
	
	/**
     *    
     * @param {int} count -  .
     * @return {token} -   {@link token}.
    */
	
	arroundRight(count){
		return this.scanner.arroundRight(this, count)
	}

	/**
     *    
     * @param {int} count -  .
     * @return {token} -   {@link token}.
    */
	
	arround(count){
		return this.scanner.arround(this, count)
	}

}

/**   */
class Scanner {
	/**
     *   . 
     * @param {Array.<*>} tokens -   .
    */
	constructor(tokens){
		tokens = tokens || []
		tokens = isArray(tokens) ? tokens : [tokens]
		this.tokens = tokens.map( (t, index) => new Token(index, t, this))
	}

	/**
     *   
     * @param {int}  -      ().
     * @return {token} -   {@link token}.
    */

	token(pos){
		pos = pos || 0
		return this.tokens[pos]
	}
	
	/**
     *     
     * @return {token} -   {@link token}.
    */
	
	first(){
		return (this.tokens.length > 0) ? this.token(0) : null
	}

	/**
     *     
     * @return {token} -   {@link token}.
    */
	
	last(){
		return (this.tokens.length > 0) ? this.token(this.tokens.length-1) : null
	}

	/**
     *      
     * @param {token} token -  {@link token}.
     * @param {int}  count - .
     
     * @return {token} -   {@link token}     {count} .
    */
	
	atLeft(token, count){
		return ((token._index - count) < 0 ) ? null : this.tokens[token._index - count]
	}	

	/**
     *      
     * @param {token} token -  {@link token}.
     * @param {int}  count - .
     
     * @return {token} -   {@link token}     {count} .
    */
	
	atRight(token, count){
		return ((token._index + count) > this.tokens.length - 1 ) ? null : this.tokens[token._index + count]
	}		
	
	/**
     *     
     * @param {token} token -  {@link token}.
     
     * @return {token} -   {@link token},   .
    */
	
	next(token){ 
		return this.atRight(token, 1)
	}	

	/**
     *     
     * @param {token} token -  {@link token}.
     
     * @return {token} -   {@link token},   .
    */

	prev(token){
		return this.atLeft(token, 1)
	}	
	
	/**
     *      
     * @param {token} token -  {@link token}.
     * @param {int} count -  .
     * @return {token} -   {@link token},   .
    */
	
	arroundLeft(token, count){
		let start = token._index - count
		start = (start < 0 ) ? 0 : start
		return slice(this.tokens, start, token._index)
	}
	
	/**
     *      
     * @param {token} token -  {@link token}.
     * @param {int} count -  .
     * @return {token} -   {@link token},   .
    */
	arroundRight(token, count){
		let stop = token._index + count + 1
		stop = (stop > this.tokens.length + 1 ) ? this.tokens.length + 1 : stop
		return slice(this.tokens, token._index+1, stop)
	}

	/**
     *      
     * @param {token} token -  {@link token}.
     * @param {int} count -  .
     * @return {token} -   {@link token},   .
    */

	arround(token, count){
		return flatten([this.arroundLeft(token, count), this.arroundRight(token, count)])
	} 

}

/**
 *   .
 *      
 * @module scanner
 */

/**
 *   {@link Scanner}.
 * @param {Array.<*>} tokens -   .
 * @return {Scanner}  {@link Scanner}
*/

export default tokens => {
	return new Scanner(tokens)
};	

